body {
  margin: 0px;
  background: #f2f7f5;
}

header {
  padding: 12px;
  background-color: #00473e;
  text-align: center;
}

header > h1 {
  margin: 0px;
}

header a {
  text-decoration: none;
  color: #fffffe;
}

table {
  border-collapse: separate;
  border-spacing: 16px 0;
}

.add-btn {
  background: #f9bc60;
  color: #001e1d;
  border: none;
  padding: 8px;
  border-radius: 20px;
  font-size: 14px;
}

.add-btn:hover {
  border: 1px solid black;
  cursor: pointer;
}

.shopping-cart-icon {
  width: 30px;
}

.nav-bar {
  padding: 24px 0px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.cart-icon {
  margin: 8px 8px 0px 0px;
  text-align: right;
}

.cart-icon span {
  align-self: baseline;
  font-size: 12px;
  background: #00473e;
  border-radius: 10px;
  color: #fffffe;
  padding: 0px 4px;
  font-weight: 700;
  margin-left: -10px;
  vertical-align: top;
}

.searchbar {
  width: 80%;
  justify-content: center;
  display: flex;
  margin: 10px 0px;
  gap: 8px;
}

.searchbar button {
  cursor: pointer;
  background: #00473e;
  color: #fffffe;
  border: none;
  padding: 8px;
  border-radius: 20px;
  font-size: 14px;
}

.searchbar input {
  width: 60%;
}

.categories {
  list-style: none;
  background: #DBEED2;
  overflow: auto;
  margin: 0px;
  padding: 0px;
  display: none;
  position: absolute;
}

.dropdown_button {
  background-color: #00473e;
	color: #fffffe;
	padding: 10px;
	font-size: 15px;
	border: none;
  border-radius: 4px;
}

aside:hover .categories {
  display: block;
}

.categories li {
  white-space: nowrap;
}

.main-page{
  display: flex;
  gap: 16px
}

.main-page p {
  margin: 16px auto;
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  align-items: stretch;
  justify-content: space-evenly;
}

.product-card {
  margin-top: 10px;
  font-size: 16px;
  background: #DBEED2;
  width: 230px;
  height: 250px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transition: 0.5s;
}

.product-card:hover {
  box-shadow: 4px -4px #008080;
}

.product-card a {
  padding: 8px;
  text-decoration: none;
  color: #001e1d;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.product-card h4 {
  margin: 0px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.product-card:hover h4 {
  display: block;
}

.product-card img {
  margin: 8px 0px;
  width: 100px;
  height: 100px;
}

.price {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 4px;
  color: #001e1d;
}

.product-details {
  padding: 20px;
  margin: 10px auto;
  font-size: 16px;
  background: #DBEED2;
  max-width: 50%;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.product-details h3 {
  margin: 0px;
}

.product-details img {
  margin: 8px 0px;
  width: 200px;
  height: 200px;
}

.product-details span {
  margin: 0px 2px;
  color: #001e1d;
}

.product-details button {
  margin-top: 10px;
}

.free-shipping{
  background: #ffd803;
  padding: 5px;
  border-radius: 10px;
}

.form-details {
  margin: 20px auto;
  gap: 8px;
  display: flex;
  flex-direction: column;
  width: fit-content;
}

.form-details textarea{
  resize: none;
}

.evaluation {
  margin: 10px auto;
  font-size: 16px;
  background: #DBEED2;
  max-width: 50%;
  border-radius: 4px;
}

.empyt-cart, .finish, .inital-text, td {
  text-align: center;
}

.cart-items{
  padding: 20px;
  margin: 10px auto;
  font-size: 16px;
  background: #DBEED2;
  max-width: 80%;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.cart-items a {
  text-decoration: none;
  background: #00473e;
  color: #fffffe;
  border: none;
  padding: 8px;
  border-radius: 20px;
  font-size: 16px;
}

.checkout {
  padding: 16px;
  display: flex;
  flex-direction: column;
  margin: 10px auto;
  font-size: 16px;
  background: #DBEED2;
  max-width: 50%;
  border-radius: 4px;
  text-align: center;
  gap: 8px;
}

.finish-btn {
  cursor: pointer;
  display: block;
  margin: auto;
  background: #00473e;
  color: #fffffe;
  border: none;
  padding: 8px;
  border-radius: 20px;
  font-size: 16px;
}